import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { BoardWrapper, MainWeatherInfo, SecondaryWeatherInfo, SecondaryItem}  from './style'
import Unit from '../Unit'
import { State } from '../../store/types'

type Main = {
  feels_like: number,
  temp_min: number,
  temp_max: number,
  humidity: number,
  pressure: number,
  temp: number,
}

type Weather = {
  description: string,
  icon: string,
  main: string,
}
interface Props {
  main: Main,
  weather: Weather,
}

export const WeatherBoard: React.FC<any> = ({
  weather,
  main,
  wind,
}) => {

  const state = useSelector((state: State) => state.weather)
  const { name } = state
  const { feels_like, temp_min, temp_max, humidity, pressure, temp } = main
  const { description, icon, main: mainDescription } = weather
  const { speed } = wind


  return (
    <BoardWrapper>
      <MainWeatherInfo>
        <div>
          {
            name !== '' 
              ? <img src={`http://openweathermap.org/img/wn/${icon}@4x.png`} />
              : <i className="fas fa-cloud-moon fa-7x"></i>
          }
        </div>
        <div>
          <p>{Math.round(temp)}&#176;</p>
          <p>{description} </p>
        </div>
      </MainWeatherInfo>
      <SecondaryWeatherInfo>
        <SecondaryItem>
          <Unit 
            temp={feels_like}
            type='deg'
          />
          <p>Feels Like</p>
        </SecondaryItem>
        <SecondaryItem>
          <Unit 
            temp={speed}
            type='mph'
          />
          <p>Wind</p>
        </SecondaryItem>
        <SecondaryItem>
          <Unit 
            temp={temp_max}
            type='deg'
          />
          <p>Max</p>
        </SecondaryItem>
        <SecondaryItem>
          <Unit 
            temp={humidity}
            type='deg'
          />
          <p>Humidity</p>
        </SecondaryItem>
        <SecondaryItem>
          <Unit 
            temp={pressure}
            type='pressure'
          />
          <p>Pressure</p>
        </SecondaryItem>
      </SecondaryWeatherInfo>
    </BoardWrapper>
  )
}
