import React from 'react'
import { useSelector } from 'react-redux'
import GlobalStyle from './globalStyle'
import {Container, WeatherContainer} from './style'
import Search from '../Search'
import Heading from '../Heading'
import WeatherBoard from '../WeatherBoard'
import { State } from '../../store/types'


export interface Props {}

export const Layout: React.FC<Props> = () => {

  const weatherMain  = useSelector<State>(state => state.weather.main)
  const weatherWind  = useSelector<State>(state => state.weather.wind)
  const weatherInfo = useSelector<State>(state => state.weather.weather[0])

  return (
    <>
      <GlobalStyle />
      <Container>
        <Heading text='Weather app - React/Typescript' />
        <Search />
        <WeatherContainer>

        <WeatherBoard
          weather={weatherInfo}
          main={weatherMain}
          wind={weatherWind}
        />
        </WeatherContainer>
      </Container>
    </>
  )
}