import React from 'react'
import { isEmpty } from 'lodash'
import { useSelector } from 'react-redux'
import UnitContainer from './style'
import { State } from '../../store/types'

interface Props {
  temp: number,
  size?: number,
  type: 'deg' | 'percentage' | 'pressure' | 'mph'
}

const Unit: React.FC<Props> = ({
  temp,
  size,
  type,
}) => {

  const state = useSelector((state: State) => state)
  const { loading, error, weather } = state

  const unitValue =
    error !== ''
      ? '-'
      : Math.round(temp)

  let symbol 

  if (type === 'deg') symbol = <>&#176;</>
  else if (type === 'percentage') symbol = <>&#37;</>
  else if (type === 'mph') symbol = 'mph'
  else symbol === ''

  return (
    <UnitContainer size={size}>

      { 
        loading 
        ? <i className="fa fa-spinner" aria-hidden="true"></i>
        : <>{unitValue}{symbol}</>
      }

      
    </UnitContainer>
  )
}

export default Unit